<script>
import { serverBus } from "@/main";
import NavTop from "./../NavTop";

import Vue from "vue";
import _ from "lodash";
import vSelect from "vue-select";
import QrcodeVue from "qrcode.vue";

export default {
  name: "users-new",

  data() {
    return {
      user_uuid: false,
      loading: false,
      edit_mode: false,
      form: {
        parent: {},
        sponsor: {},
        rank_recognition: "",
        rank: "",
        network_validated: 0,
      },
      options: [],
    };
  },
  computed: {
    nviMax() {
      return this.form.network_validated > 10 ? true : false;
    },
  },
  components: {
    "top-nav": NavTop,
    "v-select": vSelect,
    "qr-code": QrcodeVue,
  },
  created() {
    if (this.$route.params.uuid) {
      this.edit_mode = true;
      this.user_uuid = this.$route.params.uuid;
      this.fetchUser(this.$route.params.uuid);
      this.listUsers();
    }
  },
  watch: {
    "form.parent_uuid": function(newVal, oldVal) {
      if (newVal) {
        let finalVal = newVal.uuid;
        if (finalVal) {
          this.form.parent_uuid = finalVal;
          this.form.sponsor_uuid = finalVal;
        }
      }
    },

    "form.ancestry": function(newVal, oldVal) {
      if (newVal && newVal.ancestry && newVal.id) {
        let finalVal = newVal.ancestry + "/" + newVal.id;
        if (finalVal) {
          this.form.ancestry = finalVal;
        }
      }
    },
  },

  methods: {
    listUsers() {
      this.loading = true;
      this.$http("/api/v1/admin/users/usernames?extended=true", {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          this.options = response.data.map((user) => {
            return {
              id: user[0],
              uuid: user[1],
              username: user[2],
              ancestry: user[3],
            };
          });
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },

    fetchUser(uuid) {
      this.loading = true;
      this.$http(`/api/v1/admin/users/${uuid}`, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          this.form = response.data;
          this.form.skip_auth_email = true;
          this.loading = false;
        })
        .catch((error) => {
          this.loading = false;
        });
    },

    updateNode(evt) {
      evt.preventDefault();
      this.loading = true;
      this.$http({
        url: "/api/v1/admin/users/" + this.form.uuid + ``,
        data: this.form,
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          let msg = this.edit_mode
            ? "User successfully updated"
            : "User successfully created";
          this.$toasted.show(msg, {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true,
          });
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.message.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },

    reset() {
      this.form = {
        publish: true,
      };
    },
  },
};
</script>
